





















































import Theme from '@/mixins/Theme'

export default Theme.extend({
  name: 'modal-default',
  mixins: [Theme],
  props: {
    data: {
      type: Object,
      default: () => ({})
    },
    isActive: {
      type: Boolean,
      default: true
    },
    canCancel: {
      type: Boolean,
      default: true
    },
    handleSubmit: {
      type: Function,
      default: (event: any) => event
    },
    bodyClasses: {
      type: String,
      default: 'pl-6 pr-6'
    }
  },
  data() {
    return {
      active: this.isActive
    }
  }
})
